.btn{
    &__primary{
        background-color: #ffcd34;
        border-radius: 2px;
        font-weight: 600;
        color: #231f20;
        font-size: 16px;
        line-height: 22px;

        &:focus{
            background-color: #e8ae00;
        }
        &:focus-visible {
            outline: none;
          }     
        &:hover {
            background-color: #fae094;
          }
        &:active {
            background: #e8ae00;
          }
        &:disabled {
            background-color: #ffffff;
            border: 1px solid #d9d9d9;
            color: #a09f9f;
        }
    }

    &__secondary{
        background-color: transparent;
        border: 1px solid #231f20;
        border-radius: 2px;
        font-weight: 600;
        color: #231f20;
        font-size: 16px;
        line-height: 22px;
        &:hover {
            border: 1px solid #ffcd34;
            background: #ffffff;
        }
        &:active {
            background: #fff9ea;
            border: 1px solid #ffcd34;
        }
        &:focus{
            border: 1px solid #ffcd34;
            outline: none;
            background: #fff9ea;
        }
        &:focus-visible {
            border: 1px solid #ffcd34;
            outline: none;
            background: #fff9ea;
        }
        &:disabled {
            background: transparent;
            border: 1px solid #d9d9d9;
            color: #a09f9f;
        }    

    }

    &__header{
        padding-left: 2rem;
        padding-right: 2rem;
        padding-top: 1rem;
        padding-bottom: 1rem;
        vertical-align: middle;
        font-size: 18px;
        line-height: 24px;
        color: #ffffff;
        border: 1px solid transparent;
        &:focus{
            border: 1px solid #ffcd34;
            outline: 1px solid #595959;
        }
        &:focus-visible {
            border: 1px solid #ffcd34;
            outline: 1px solid #595959;
        }
               
    }
 

}


